/* Full page layout with some margin around the rectangle */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: black; /* Terminal-like black background */
  font-family: monospace;  /* Monospace font for terminal look */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Green border around the console-like container, almost full window */
.console-container {
  border: 4px solid #00ff00; /* Thicker green border */
  padding: 20px;
  width: 90vw; /* 90% of the viewport width */
  height: 90vh; /* 90% of the viewport height */
  display: flex;
  flex-direction: column; /* Ensure buttons appear under text */
  justify-content: start;
  align-items: start;
  color: #00ff00; /* Green text */
  background-color: black; /* Black background inside the container */
  overflow: hidden;
  white-space: pre-wrap; /* Preserve white space and line breaks */
}

/* Ensure text and buttons are in separate areas */
.console-text-wrapper {
  flex-grow: 1; /* Allow text to take up the most space */
  width: 100%;
}

/* Text styling */
.console-text {
  font-size: 1.5rem; /* Larger font size for readability */
  line-height: 1.6; /* Line height for better spacing */
  width: 100%;
}

/* Blinking cursor */
.cursor {
  display: inline-block;
  width: 10px;
  background-color: #00ff00; /* Green cursor */
  animation: blink 1s steps(1) infinite; /* Blinking effect */
}

@keyframes blink {
  50% {
    opacity: 0;
  }
}

/* Interactive text (buttons) */
.console-button {
  color: #00ff00;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5rem; /* Match text size */
  line-height: 1.6; /* Match line height */
}

/* Button wrapper to make each button block-level */
.console-button-wrapper {
  margin-top: 10px; /* Adds spacing between buttons */
  width: 100%;
}

/* Add underline effect to hover */
.console-button:hover {
  text-decoration: underline;
}

/* Button container to display the buttons after typing is complete */
.button-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column; /* Stack buttons vertically */
  width: 100%;
}
